.implementation {
  padding: 100px 0;
  background-color: #f8f9fc;
}

.section-subtitle {
  text-align: center;
  margin-bottom: 40px;
  color: var(--light-text);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1rem;
  line-height: 1.6;
}

.subsection-title {
  text-align: center;
  color: var(--primary-color);
  margin: 60px 0 30px;
  font-size: 1.6rem;
  position: relative;
}

.subsection-title:after {
  content: '';
  display: block;
  width: 80px;
  height: 3px;
  background: var(--secondary-color);
  margin: 15px auto 0;
}

/* Budget Timeline Styles */
.budget-timeline {
  display: flex;
  justify-content: space-between;
  margin: 40px 0;
  align-items: stretch;
  flex-wrap: wrap;
}

.budget-item {
  flex: 1;
  min-width: 250px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.budget-content {
  background: white;
  border-radius: 8px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  height: 100%;
  width: 100%;
  transition: all 0.3s ease;
}

.budget-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.budget-title {
  font-size: 1.3rem;
  color: var(--primary-color);
  margin-bottom: 10px;
  font-weight: 600;
}

.budget-amount {
  font-size: 1.4rem;
  font-weight: bold;
  color: var(--secondary-color);
  margin-bottom: 15px;
  padding: 8px 0;
  border-bottom: 1px solid rgba(63, 81, 181, 0.1);
}

.budget-details {
  margin-bottom: 15px;
}

.detail-label, .outcomes-label {
  font-weight: 600;
  color: var(--primary-color);
  font-size: 0.9rem;
}

.budget-scope, .budget-timeline-detail {
  margin-bottom: 5px;
  font-size: 0.95rem;
}

.budget-outcomes ul {
  list-style-type: none;
  padding: 0;
  margin-top: 8px;
}

.budget-outcomes li {
  position: relative;
  padding-left: 20px;
  margin-bottom: 8px;
  font-size: 0.9rem;
  line-height: 1.5;
}

.budget-outcomes li:before {
  content: "•";
  position: absolute;
  left: 5px;
  color: var(--secondary-color);
  font-weight: bold;
}

.budget-arrow {
  font-size: 2rem;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  padding: 0 10px;
}

/* Consideration Note */
.consideration-note {
  background-color: rgba(63, 81, 181, 0.05);
  border-left: 4px solid var(--primary-color);
  padding: 20px 25px;
  margin: 40px 0;
  border-radius: 0 8px 8px 0;
}

.consideration-note h4 {
  color: var(--primary-color);
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.consideration-note p {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-color);
}

/* Pilot Project Styles */
.pilots-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 25px;
  margin-top: 40px;
}

.pilot-card {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: all 0.3s ease;
}

.pilot-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.pilot-icon {
  font-size: 2.5rem;
  padding: 20px;
  background-color: rgba(63, 81, 181, 0.1);
  text-align: center;
}

.pilot-content {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.pilot-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.pilot-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 15px;
}

.pilot-benefits {
  margin-top: 15px;
  margin-bottom: 20px;
  flex-grow: 1;
}

.pilot-benefits h5 {
  font-size: 1rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.pilot-benefits ul {
  list-style-type: none;
  padding: 0;
}

.pilot-benefits li {
  position: relative;
  padding-left: 20px;
  margin-bottom: 8px;
  font-size: 0.9rem;
  line-height: 1.5;
}

.pilot-benefits li:before {
  content: "→";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
}

.pilot-budget {
  margin-top: auto;
  padding-top: 15px;
  border-top: 1px dashed rgba(63, 81, 181, 0.2);
  font-size: 0.95rem;
}

.budget-label {
  font-weight: 600;
  color: var(--primary-color);
}

/* Public Value Note */
.public-value-note {
  background-color: rgba(76, 175, 80, 0.05);
  border-radius: 8px;
  padding: 25px;
  margin: 50px 0 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.public-value-note h4 {
  color: #4caf50;
  margin-bottom: 15px;
  font-size: 1.2rem;
  text-align: center;
}

.public-value-note p {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 20px;
}

.value-points {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.value-point {
  background: white;
  border-radius: 30px;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
}

.value-icon {
  font-size: 1.2rem;
}

.value-text {
  font-size: 0.9rem;
  color: var(--text-color);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .implementation {
    padding: 60px 0;
  }
  
  .budget-timeline {
    flex-direction: column;
  }
  
  .budget-arrow {
    transform: rotate(90deg);
    margin: 10px 0;
  }
  
  .pilots-grid {
    grid-template-columns: 1fr;
  }
  
  .value-points {
    flex-direction: column;
    align-items: center;
  }
}

/* 내부 구성원 참여 모델 스타일 */
.participation-intro {
  text-align: center;
  margin-bottom: 40px;
  color: var(--light-text);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.05rem;
  line-height: 1.6;
}

.participation-timeline {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 40px 0;
}

.participation-item {
  display: flex;
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.participation-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.participation-stage {
  width: 180px;
  padding: 25px 15px;
  background-color: var(--primary-color);
  color: white;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.1rem;
}

.participation-content {
  flex: 1;
  padding: 25px;
}

.participation-activities {
  list-style-type: none;
  padding: 0;
  margin: 0 0 15px 0;
}

.participation-activities li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 8px;
  font-size: 0.95rem;
  line-height: 1.5;
}

.participation-activities li:before {
  content: "•";
  position: absolute;
  left: 5px;
  color: var(--secondary-color);
  font-weight: bold;
}

.participation-outcomes {
  font-size: 0.95rem;
  color: var(--text-color);
  line-height: 1.6;
  padding-top: 10px;
  border-top: 1px dashed rgba(63, 81, 181, 0.2);
}

/* 가치 측정 프레임워크 스타일 */
.value-metrics-intro {
  text-align: center;
  margin-bottom: 40px;
  color: var(--light-text);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.05rem;
  line-height: 1.6;
}

.value-metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  margin: 40px 0;
}

.value-metric-card {
  background: white;
  border-radius: 8px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.value-metric-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.value-metric-icon {
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-align: center;
}

.value-metric-category {
  font-size: 1.2rem;
  color: var(--primary-color);
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
}

.value-metric-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.value-metric-list li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 12px;
  font-size: 0.95rem;
  line-height: 1.5;
  color: var(--text-color);
}

.value-metric-list li:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-weight: bold;
}

/* 실험 중심 접근법 스타일 */
.experimental-approach-note {
  background-color: rgba(76, 175, 80, 0.05);
  border-radius: 8px;
  padding: 25px;
  margin: 50px 0 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.experimental-approach-note h4 {
  color: var(--primary-color);
  margin-bottom: 15px;
  font-size: 1.2rem;
  text-align: center;
}

.experimental-approach-note p {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 25px;
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.experiment-cycle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}

.cycle-step {
  background: white;
  border-radius: 8px;
  padding: 15px;
  min-width: 100px;
  text-align: center;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
}

.cycle-icon {
  font-size: 2rem;
  margin-bottom: 10px;
}

.cycle-title {
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-color);
}

.cycle-arrow {
  font-size: 1.5rem;
  color: var(--primary-color);
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .participation-item {
    flex-direction: column;
  }
  
  .participation-stage {
    width: 100%;
    padding: 15px;
  }
  
  .experiment-cycle {
    flex-direction: column;
  }
  
  .cycle-arrow {
    transform: rotate(90deg);
  }
} 