.features {
  background-color: var(--light-background);
  padding: 100px 0;
}

.section-subtitle {
  text-align: center;
  margin-bottom: 40px;
  color: var(--light-text);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1rem;
  line-height: 1.6;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 50px;
}

.feature-card {
  background: white;
  border-radius: 8px;
  padding: 30px;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.feature-icon {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: var(--primary-color);
  display: inline-block;
}

.feature-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: var(--primary-color);
}

.feature-description {
  color: var(--light-text);
  line-height: 1.6;
  margin-bottom: 20px;
}

.feature-details {
  text-align: left;
  margin-top: 20px;
  margin-bottom: 20px;
}

.feature-details h4 {
  font-size: 1.1rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.feature-details ul {
  list-style-type: none;
  padding: 0;
}

.feature-details li {
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
  line-height: 1.5;
  font-size: 0.95rem;
}

.feature-details li:before {
  content: "→";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-weight: bold;
}

.feature-highlight-tag {
  margin-top: auto;
  padding-top: 15px;
}

.feature-highlight-tag span {
  display: inline-block;
  background-color: rgba(63, 81, 181, 0.1);
  color: var(--primary-color);
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

.feature-expansion {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(63, 81, 181, 0.03);
  border-radius: 8px;
  text-align: left;
}

.feature-expansion h4 {
  font-size: 1rem;
  margin-bottom: 12px;
  color: var(--primary-color);
}

.feature-expansion ul {
  padding: 0;
  list-style-type: none;
}

.feature-expansion li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 10px;
  font-size: 0.9rem;
  line-height: 1.5;
}

.feature-expansion li:before {
  content: "↗";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-weight: bold;
}

.feature-products {
  margin-top: 15px;
}

.feature-international-case {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(76, 175, 80, 0.1);
  border-radius: 8px;
  border-left: 3px solid #4caf50;
}

.feature-international-case h4 {
  color: var(--primary-color);
  font-size: 1.05rem;
  margin-bottom: 10px;
}

.feature-international-case .case-title {
  font-weight: 600;
  font-size: 0.95rem;
  color: #4caf50;
  margin-bottom: 8px;
}

.feature-international-case .case-description {
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: 8px;
  color: var(--light-text);
}

.feature-international-case .case-impact {
  font-size: 0.9rem;
  line-height: 1.5;
  font-style: italic;
  color: var(--text-color);
  margin-top: 10px;
  padding-top: 8px;
  border-top: 1px dashed rgba(76, 175, 80, 0.3);
}

.feature-card strong {
  color: var(--primary-color);
  font-weight: 600;
}

.feature-products h4 {
  font-size: 1.05rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.product-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.product-tag {
  display: inline-block;
  padding: 6px 12px;
  background-color: rgba(63, 81, 181, 0.1);
  color: var(--primary-color);
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.product-tag:hover {
  background-color: rgba(63, 81, 181, 0.15);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .features {
    padding: 80px 0;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .feature-card {
    padding: 25px;
  }
  
  .feature-icon {
    font-size: 2rem;
    margin-bottom: 15px;
  }
  
  .feature-title {
    font-size: 1.3rem;
    margin-bottom: 10px;
  }
  
  .feature-details li {
    font-size: 0.9rem;
  }
}

.features-section {
  background-color: var(--bg-light);
  padding: 80px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 60px;
}

.section-title h2 {
  color: var(--primary-color);
  font-size: 2.5rem;
  margin-bottom: 15px;
}

.section-title p {
  color: var(--light-text);
  font-size: 1.2rem;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.feature-card {
  background-color: white;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.feature-icon-wrapper {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 20px;
  text-align: center;
}

.feature-title {
  font-size: 1.4rem;
  color: var(--text-color);
  margin-bottom: 15px;
  text-align: center;
}

.feature-description {
  font-size: 1rem;
  color: var(--text-color);
  line-height: 1.6;
  margin-bottom: 25px;
}

.feature-description strong {
  color: var(--primary-color);
  font-weight: 600;
}

.feature-experiment-stage,
.feature-social-impact {
  background-color: rgba(63, 81, 181, 0.05);
  border-radius: 8px;
  padding: 15px;
  margin-top: 20px;
}

.feature-experiment-stage h4,
.feature-social-impact h4 {
  font-size: 1rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.feature-experiment-stage p,
.feature-social-impact p {
  font-size: 0.9rem;
  color: var(--text-color);
  line-height: 1.5;
}

.feature-social-impact {
  background-color: rgba(76, 175, 80, 0.05);
  border-left: 3px solid var(--secondary-color);
}

/* 실험 중심 접근법 스타일 */
.approach-section {
  background-color: white;
  border-radius: 10px;
  padding: 40px;
  margin-bottom: 50px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.approach-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 15px;
}

.approach-description {
  font-size: 1.1rem;
  color: var(--text-color);
  line-height: 1.6;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 30px;
}

.experiment-steps {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.experiment-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(63, 81, 181, 0.05);
  border-radius: 8px;
  padding: 15px;
  min-width: 100px;
}

.step-icon {
  font-size: 2rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.step-label {
  font-size: 0.9rem;
  color: var(--text-color);
  font-weight: 500;
}

.step-arrow {
  font-size: 1.5rem;
  color: var(--primary-color);
}

/* 지역사회 참여 모델 스타일 */
.engagement-section {
  background-color: white;
  border-radius: 10px;
  padding: 40px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.engagement-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 15px;
}

.engagement-description {
  font-size: 1.1rem;
  color: var(--text-color);
  line-height: 1.6;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 30px;
}

.community-groups {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.community-group {
  background-color: rgba(76, 175, 80, 0.05);
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  transition: all 0.3s ease;
}

.community-group:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
}

.group-icon {
  font-size: 2rem;
  color: var(--secondary-color);
  margin-bottom: 15px;
}

.group-label {
  font-size: 1.1rem;
  color: var(--primary-color);
  font-weight: 600;
  margin-bottom: 10px;
}

.group-role {
  font-size: 0.9rem;
  color: var(--text-color);
  line-height: 1.5;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .feature-grid {
    grid-template-columns: 1fr;
  }
  
  .experiment-steps {
    flex-direction: column;
  }
  
  .step-arrow {
    transform: rotate(90deg);
    margin: 5px 0;
  }
  
  .community-groups {
    grid-template-columns: 1fr;
  }
  
  .approach-section,
  .engagement-section {
    padding: 30px 20px;
  }
} 