.cases {
  padding: 100px 0;
  background-color: #fff;
}

.section-subtitle {
  text-align: center;
  margin-bottom: 40px;
  color: var(--light-text);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1rem;
  line-height: 1.6;
}

.cases-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 50px;
}

.case-card {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.case-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.case-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.case-image .image-placeholder {
  width: 100%;
  height: 100%;
  background-color: rgba(63, 81, 181, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: var(--primary-color);
}

.case-icon {
  font-size: 4rem;
  font-weight: bold;
  color: rgba(63, 81, 181, 0.6);
  background: rgba(255, 255, 255, 0.5);
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.case-content {
  padding: 25px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.case-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 8px;
  color: var(--primary-color);
}

.case-broadcaster {
  font-size: 0.9rem;
  color: var(--secondary-color);
  margin-bottom: 15px;
  font-weight: 500;
}

.case-description {
  color: var(--light-text);
  line-height: 1.6;
  font-size: 0.95rem;
  margin-bottom: 20px;
}

.case-results {
  margin-bottom: 20px;
}

.case-results h4 {
  font-size: 1.05rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.case-results ul {
  list-style-type: none;
  padding: 0;
}

.case-results li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 8px;
  font-size: 0.9rem;
  line-height: 1.5;
}

.case-results li:before {
  content: "•";
  position: absolute;
  left: 5px;
  color: var(--secondary-color);
  font-weight: bold;
}

.case-quote {
  background-color: rgba(63, 81, 181, 0.05);
  padding: 15px;
  border-left: 3px solid var(--primary-color);
  margin-top: auto;
}

.case-quote blockquote {
  margin: 0 0 10px 0;
  font-style: italic;
  font-size: 0.95rem;
  line-height: 1.5;
  color: var(--text-color);
}

.case-quote cite {
  display: block;
  text-align: right;
  font-size: 0.85rem;
  color: var(--light-text);
  font-style: normal;
  font-weight: 500;
}

.case-future-vision {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(63, 81, 181, 0.05);
  border-radius: 8px;
}

.case-future-vision h4 {
  font-size: 1.05rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.case-future-vision p {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-color);
  font-style: italic;
}

.case-development-plan {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(63, 81, 181, 0.03);
  border-radius: 8px;
  border-left: 3px solid var(--primary-color);
}

.case-development-plan h4 {
  font-size: 1.05rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.development-item {
  margin-bottom: 12px;
}

.development-item:last-child {
  margin-bottom: 0;
}

.development-tag {
  display: inline-block;
  background-color: var(--primary-color);
  color: white;
  padding: 4px 10px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
  margin-bottom: 6px;
}

.development-item p {
  margin: 0;
  font-size: 0.9rem;
  line-height: 1.5;
  color: var(--light-text);
}

.case-international-reference {
  margin-top: 25px;
  padding: 15px;
  background-color: rgba(63, 81, 181, 0.03);
  border-radius: 8px;
  border-left: 3px solid #4caf50;
}

.case-international-reference h4 {
  font-size: 1.05rem;
  color: var(--primary-color);
  margin-bottom: 12px;
}

.reference-title {
  font-weight: 600;
  font-size: 0.95rem;
  color: #4caf50;
  margin-bottom: 8px;
}

.case-international-reference p {
  font-size: 0.9rem;
  line-height: 1.5;
  color: var(--light-text);
  margin-bottom: 10px;
}

.reference-source {
  font-size: 0.8rem;
  color: var(--light-text);
  font-style: italic;
  margin-bottom: 12px;
}

.reference-learnings {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px dashed rgba(63, 81, 181, 0.2);
}

.learnings-tag {
  display: inline-block;
  background-color: #4caf50;
  color: white;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 500;
  margin-bottom: 6px;
}

.reference-learnings p {
  margin: 8px 0 0;
  font-size: 0.9rem;
  line-height: 1.5;
}

.case-card strong {
  color: var(--primary-color);
  font-weight: 600;
}

@media (max-width: 768px) {
  .cases {
    padding: 60px 0;
  }
  
  .cases-grid {
    grid-template-columns: 1fr;
  }
  
  .case-image {
    height: 180px;
  }
  
  .case-icon {
    font-size: 3rem;
    width: 80px;
    height: 80px;
  }
}

.cases-section {
  background-color: var(--bg-light);
  padding: 80px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 60px;
}

.section-title h2 {
  color: var(--primary-color);
  font-size: 2.5rem;
  margin-bottom: 15px;
}

.section-title p {
  color: var(--light-text);
  font-size: 1.2rem;
}

.case-card {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 40px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.case-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.case-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.case-description {
  font-size: 1.05rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 25px;
}

.case-learnings {
  background-color: rgba(63, 81, 181, 0.05);
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 30px;
}

.case-learnings h4 {
  color: var(--primary-color);
  font-size: 1.3rem;
  margin-bottom: 15px;
}

.case-learnings ul {
  list-style-type: none;
  padding: 0;
}

.case-learnings li {
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  font-size: 1rem;
  line-height: 1.5;
}

.case-learnings li:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-weight: bold;
}

/* 단계적 접근법 스타일 */
.phase-approach {
  margin-bottom: 30px;
}

.phase-approach h4 {
  color: var(--primary-color);
  font-size: 1.3rem;
  margin-bottom: 20px;
}

.phases {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.phase-card {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.phase-card:hover {
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

.phase-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.phase-header h5 {
  font-size: 1.1rem;
  color: var(--primary-color);
  margin: 0;
}

.phase-period {
  background-color: rgba(63, 81, 181, 0.1);
  color: var(--text-color);
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.phase-activities {
  list-style-type: none;
  padding: 0;
  margin-bottom: 15px;
}

.phase-activities li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 8px;
  font-size: 0.95rem;
  line-height: 1.5;
}

.phase-activities li:before {
  content: "•";
  position: absolute;
  left: 5px;
  color: var(--secondary-color);
  font-weight: bold;
}

.phase-outcome {
  border-top: 1px dashed rgba(0, 0, 0, 0.1);
  padding-top: 15px;
  font-size: 0.95rem;
  color: var(--text-color);
  line-height: 1.6;
}

.phase-outcome strong {
  color: var(--primary-color);
  font-weight: 600;
}

/* 실험 중심 접근법 스타일 */
.experimental-approach {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 40px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.approach-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 15px;
}

.approach-description {
  font-size: 1.1rem;
  color: var(--text-color);
  line-height: 1.6;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 30px;
}

.principles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 25px;
}

.principle-card {
  background-color: rgba(63, 81, 181, 0.02);
  border-radius: 8px;
  padding: 25px;
  text-align: center;
  transition: all 0.3s ease;
}

.principle-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
}

.principle-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.principle-title {
  font-size: 1.2rem;
  color: var(--primary-color);
  margin-bottom: 12px;
}

.principle-description {
  font-size: 0.95rem;
  color: var(--text-color);
  line-height: 1.6;
}

/* 한국 적용 가이드 스타일 */
.adaptation-guide {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 40px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.guide-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 15px;
}

.guide-description {
  font-size: 1.1rem;
  color: var(--text-color);
  line-height: 1.6;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 30px;
}

.recommendations-list {
  background-color: rgba(76, 175, 80, 0.05);
  border-radius: 8px;
  padding: 25px;
  margin: 0 auto;
  max-width: 800px;
  list-style-type: none;
}

.recommendation-item {
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-color);
}

.recommendation-item:last-child {
  margin-bottom: 0;
}

.recommendation-item:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-size: 1.1rem;
  font-weight: bold;
}

/* 참고 문헌 스타일 */
.case-international-reference {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.reference-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  margin-bottom: 20px;
  text-align: center;
}

.reference-source {
  margin-bottom: 25px;
  text-align: center;
}

.reference-source p {
  margin-bottom: 10px;
  font-size: 1rem;
  color: var(--text-color);
}

.reference-source a {
  color: var(--primary-color);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
}

.reference-source a:hover {
  border-bottom-color: var(--primary-color);
}

.reference-learnings {
  background-color: rgba(63, 81, 181, 0.05);
  border-radius: 8px;
  padding: 20px;
  text-align: center;
}

.reference-learnings p {
  font-size: 1.05rem;
  color: var(--text-color);
  line-height: 1.7;
}

.reference-learnings strong {
  color: var(--primary-color);
  font-weight: 600;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .principles-grid {
    grid-template-columns: 1fr;
  }
  
  .case-card,
  .experimental-approach,
  .adaptation-guide,
  .case-international-reference {
    padding: 20px;
  }
  
  .case-title,
  .approach-title,
  .guide-title,
  .reference-title {
    font-size: 1.5rem;
  }
  
  .phase-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
} 